import React, { useState, useEffect } from 'react';

const LastMonthDetail = ({Nothingtoshow}) => {

  const [isData, setIsData] = useState(false);
  const [employeeLength, setEmployeeLength] = useState([]);
  const [projectLength, setProjectLength] = useState([]);
  const [clientLength, setClientLength] = useState([]);

  useEffect(() => {
    const employeeCount = localStorage.getItem('Employes');
    const employeeLengthData = JSON.parse(employeeCount);
    setEmployeeLength(employeeLengthData);

    const projectCount = localStorage.getItem('addnewproject');
    const projectLengthData = JSON.parse(projectCount);
    setProjectLength(projectLengthData);
    setClientLength(projectLengthData);

    setIsData(!!projectLengthData && !!employeeLengthData);
  }, []);

  const cardsData = [
    { title: 'Total Employees', number: employeeLength.length , icon: 'fa-solid fa-people-group', iconbg: 'purple', textcolor: '#02eff7' },
    { title: 'Total Projects', number: projectLength.length, icon: 'fa-solid fa-file-invoice', iconbg: '#02eff7', textcolor: 'red' },
    { title: 'Total Clients', number: projectLength.length, icon: 'fa-solid fa-hand-holding-dollar', iconbg: '#CAE44F', textcolor: '#32CD32' },
  ];

  return (
    <div className='row g-1'>
      {isData ? (
        cardsData.map((data, index) => (
          <div key={index} className="col-md-4 mb-2">
            <div className="card">
              <div className="card-body rounded d-flex justify-content-between">
                <div>
                  <h5 className='card-title fw-bold '>{data.title || 'Not found'}</h5>
                  <span className='fw-bold small'>{data.number || 'Not found'}</span>
                  <p className='card-text fw-bold small mt-3' style={{ color: `${data.textcolor}` }}>
                    {data.number}
                    <span className='small ms-1 text-dark'>since last month</span>
                  </p>
                </div>
                <div className='card_icons'>
                  <i className={`${data.icon} rounded-pill fs-3 d-flex justify-content-center align-items-center text-light`} style={{ background: `${data.iconbg}` }}></i>
                </div>
              </div>
            </div>
          </div>
        ))
      ) : (
        <div className="card mb-2">

          <div className="card-body text-danger text-center fw-bold">
            <Nothingtoshow />
          </div>

        </div>
      )}
    </div>
  );
};

export default LastMonthDetail;
